import en from './en'
import cn from './cn'
import { getLocale, saveLocale } from '../utils/localStorage'
import { createI18n } from 'vue-i18n'

const messages = {
    en,
    cn
}

let locale = getLocale()
if (!locale) {
    locale = 'cn'
    saveLocale(locale)
}

const i18n = createI18n({
    locale, // 默认语言
    messages // 引入语言文件
})

export default i18n
