<template>
    <transition name="slide-up">
        <div
            class="setting-wrapper"
            v-show="menuVisible && settingVisible === 2"
        >
            <div class="setting-progress">
                <div class="read-time-wrapper">
                    <span class="read-time-text">{{ getReadTimeText() }}</span>
                    <span class="icon-forward"></span>
                </div>
                <div class="progress-wrapper">
                    <div class="progress-icon-wrapper" @click="prevSection()">
                        <span class="icon-back"></span>
                    </div>
                    <input
                        class="progress"
                        type="range"
                        max="100"
                        min="0"
                        step="1"
                        @change="onProgressChange($event.target.value)"
                        @input="onProgressInput($event.target.value)"
                        :value="progress"
                        :disabled="!bookAvailable"
                        ref="progress"
                    />
                    <div class="progress-icon-wrapper" @click="nextSection()">
                        <span class="icon-forward"></span>
                    </div>
                </div>
                <div class="text-wrapper">
                    <span class="progress-section-text">{{
                        getSectionName
                    }}</span>
                    <span
                        >({{ bookAvailable ? progress + '%' : '加载中...' }})
                    </span>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
import { ebookMixin } from '../../utils/mixin'
export default {
    mixins: [ebookMixin],
    computed: {
        getSectionName() {
            // if (this.section) {
            //     const sectionInfo = this.currentBook.section(this.section)
            //     if (
            //         sectionInfo &&
            //         sectionInfo.href &&
            //         this.currentBook &&
            //         this.currentBook.navigation
            //     ) {
            //         // 只能获取一级目录
            //         return this.currentBook.navigation.get(sectionInfo.href)
            //         // .label
            //     }
            // }
            // return ''
            // 认为section是拉直后导航的下标
            if (this.navigation && this.navigation.length) {
                return this.section ? this.navigation[this.section].label : ''
            }
            return ''
        }
    },
    methods: {
        onProgressChange(progress) {
            // this.setProgress(progress).then(() => {
            //     const cfi = this.currentBook.locations.cfiFromPercentage(
            //         this.progress / 100
            //     )
            //     this.display(cfi)
            // })
            if (this.pagelist) {
                const cfi = this.currentBook.locations.cfiFromPercentage(
                    progress / 100
                )
                const index = Math.floor(
                    (progress / 100) * this.pagelist.length
                )
                this.display(cfi, index)
            }
        },
        onProgressInput(progress) {
            this.setProgress(progress)
        },
        updateProgressBg() {
            // 下面这种方式会被覆盖掉
            // this.$refs.progress.style.backgroundSize = `${this.progress}% 100%`
            this.$refs.progress.style.cssText = `background-size: ${this.progress}% 100% !important`
        },
        prevSection() {
            if (this.section > 0 && this.bookAvailable) {
                this.displaySection(this.section - 1)
            }
        },
        nextSection() {
            if (
                this.bookAvailable &&
                this.section < this.navigation.length - 1
            ) {
                this.displaySection(this.section + 1)
            }
        },
        displaySection(section) {
            const sectionInfo = this.navigation[section]
            if (sectionInfo && sectionInfo.href && sectionInfo.page) {
                this.display(sectionInfo.href, sectionInfo.page)
            }
        }
    },
    // updated有时可能并不会执行
    // updated() {
    //     console.log('updated')
    //     this.updateProgressBg()
    //     console.log(this.$refs.progress)
    // },
    mounted() {
        this.updateProgressBg()
    },
    watch: {
        progress() {
            this.updateProgressBg()
        }
    }
}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
@import '../../assets/styles/global';

.setting-wrapper {
    position: absolute;
    bottom: px2rem(48);
    left: 0;
    z-index: 190;
    width: 100%;
    height: px2rem(90);
    box-shadow: 0 px2rem(-8) px2rem(8) rgba(0, 0, 0, 0.15);
    .setting-progress {
        position: relative;
        width: 100%;
        height: 100%;
        .read-time-wrapper {
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: px2rem(40);
            @include center;
            font-size: px2rem(12);
        }
        .progress-wrapper {
            width: 100%;
            height: 100%;
            @include center;
            padding: 0 px2rem(15);
            box-sizing: border-box;
            .progress {
                flex: 1;
                width: 100%;
                -webkit-appearance: none;
                height: px2rem(2);
                background: -webkit-linear-gradient(#5d6268, #5d6268) no-repeat,
                    #b4b5b7;
                background-size: 0 100%;
                margin: 0 px2rem(10);
                &:focus {
                    outline: none;
                }
                &::-webkit-slider-thumb {
                    -webkit-appearance: none;
                    height: px2rem(20);
                    width: px2rem(20);
                    border-radius: 50%;
                    background: #ceced0;
                    box-shadow: 0 px2rem(4) px2rem(6) 0 rgba(0, 0, 0, 0.15);
                    border: none;
                }
            }
            .progress-icon-wrapper {
                flex: 0 0 px2rem(22);
                font-size: px2rem(22);
                @include center;
            }
        }
        .text-wrapper {
            position: absolute;
            left: 0;
            bottom: px2rem(5);
            width: 100%;
            font-size: px2rem(12);
            text-align: center;
            padding: 0 px2rem(15);
            box-sizing: border-box;
            @include center;
            .progress-section-text {
                line-height: px2rem(15);
                @include ellipsis;
            }
        }
    }
}
</style>
