<template>
    <transition name="fade">
        <div
            v-show="menuVisible && settingVisible === 3"
            class="slide-content-wrapper"
        >
            <transition name="fade-slide-right">
                <div v-if="settingVisible == 3" class="content">
                    <div class="content-page-wrapper" v-if="bookAvailable">
                        <div class="content-page">
                            <component
                                :is="currentTab === 1 ? content : bookmark"
                            ></component>
                        </div>
                        <div class="content-page-tab">
                            <div
                                class="content-page-tab-item"
                                :class="{ selected: currentTab === 1 }"
                                @click="selectTab(1)"
                            >
                                {{ $t('book.navigation') }}
                            </div>
                            <div
                                class="content-page-tab-item"
                                :class="{ selected: currentTab === 2 }"
                                @click="selectTab(2)"
                            >
                                {{ $t('book.bookmark') }}
                            </div>
                        </div>
                    </div>
                    <div class="content-empty" v-else>
                        <ebook-loading></ebook-loading>
                    </div>
                </div>
            </transition>
            <div class="content-bg" @click="hideTitleAndMenu()"></div>
        </div>
    </transition>
</template>

<script>
import { ebookMixin } from '@/utils/mixin'
import EbookSlideContents from './EbookSlideContents'
import EbookSlideBookmark from './EbookSlideBookmark'
import EbookLoading from './EbookLoading'
export default {
    mixins: [ebookMixin],
    components: {
        EbookSlideContents,
        EbookSlideBookmark,
        EbookLoading
    },
    data() {
        return {
            currentTab: 1,
            selected: 'selected',
            content: 'ebook-slide-contents',
            bookmark: 'ebook-slide-bookmark'
        }
    },
    methods: {
        selectTab(value) {
            this.currentTab = value
        }
    }
}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
@import '../../assets/styles/global';

.slide-content-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 350;
    display: flex;
    width: 100%;
    height: 100%;
    .content {
        flex: 0 0 85%;
        width: 85%;
        height: 100%;
        .content-page-wrapper {
            display: flex;
            flex-direction: column;
            width: 100%;
            height: 100%;
            .content-page {
                flex: 1;
                width: 100%;
                overflow: hidden;
            }
            .content-page-tab {
                display: flex;
                flex: 0 0 px2rem(48);
                .content-page-tab-item {
                    flex: 1;
                    font-size: px2rem(12);
                    @include center;
                }
            }
        }
        .content-empty {
            width: 100%;
            height: 100%;
            @include center;
            font-size: px2rem(16);
            color: #333;
        }
    }
    .content-bg {
        flex: 0 0 15%;
        width: 15%;
        height: 100%;
    }
}
</style>
