<template>
    <div class="ebook-reader">
        <div id="read"></div>
        <div
            class="ebook-reader-mask"
            @click="onMaskClick"
            @touchmove="move"
            @touchend="moveEnd"
            @mousedown.left="onMouseEnter"
            @mousemove.left="onMouseMove"
            @mouseup.left="onMouseEnd"
        ></div>
    </div>
</template>

<script>
import { ebookMixin } from '../../utils/mixin'
import Epub from 'epubjs'
import {
    getFontFamily,
    getFontSize,
    saveFontFamily,
    saveFontSize,
    getTheme,
    saveTheme,
    getLocation
} from '../../utils/localStorage'
import { flatten } from '@/utils/book'
import { getLocalForage } from '../../utils/localForage'

global.epub = Epub
export default {
    mixins: [ebookMixin],
    methods: {
        // 1 - 鼠标进入
        // 2 - 鼠标进入后的移动
        // 3 - 鼠标从移动状态松手
        // 4 - 鼠标还原
        onMouseEnter(e) {
            this.mouseState = 1
            this.mouseStartTime = e.timeStamp
            e.preventDefault()
            e.stopPropagation()
        },
        onMouseMove(e) {
            if (this.mouseState === 1) {
                this.mouseState = 2
            } else if (this.mouseState === 2) {
                let offsetY = 0
                if (this.firstOffsetY) {
                    offsetY = e.clientY - this.firstOffsetY
                    this.setOffsetY(offsetY)
                } else {
                    this.firstOffsetY = e.clientY
                }
            }
            e.preventDefault()
            e.stopPropagation()
        },
        onMouseEnd(e) {
            if (this.mouseState === 2) {
                this.setOffsetY(0)
                this.firstOffsetY = 0
                this.mouseState = 3
            } else {
                this.mouseState = 4
            }
            const time = e.timeStamp - this.mouseStartTime
            if (time < 100) {
                this.mouseState = 4
            }
            e.preventDefault()
            e.stopPropagation()
        },
        move(e) {
            let offsetY = 0
            if (this.firstOffsetY) {
                offsetY = e.changedTouches[0].clientY - this.firstOffsetY
                this.setOffsetY(offsetY)
            } else {
                this.firstOffsetY = e.changedTouches[0].clientY
            }
            e.preventDefault()
            e.stopPropagation()
        },
        moveEnd(e) {
            this.setOffsetY(0)
            this.firstOffsetY = 0
        },
        onMaskClick(e) {
            if (
                this.mouseState &&
                (this.mouseState === 2 || this.mouseState === 3)
            ) {
                return
            }
            const offsetX = e.offsetX
            const width = window.innerWidth
            if (offsetX > 0 && offsetX < width * 0.3) {
                this.prevPage()
            } else if (offsetX > 0 && offsetX > width * 0.7) {
                this.nextPage()
            } else {
                this.toggleTitleAndMenu()
            }
        },
        prevPage() {
            if (this.rendition && this.pagelist) {
                this.rendition.prev().then(() => {
                    // this.refreshLocation()
                    this.myRefreshLocation(
                        this.pagelist[this.currentPageIndex - 1]
                    )
                })
                this.hideTitleAndMenu()
            }
        },
        nextPage() {
            if (this.rendition && this.pagelist) {
                this.rendition.next().then(() => {
                    // this.refreshLocation()
                    this.myRefreshLocation(
                        this.pagelist[this.currentPageIndex + 1]
                    )
                })
                this.hideTitleAndMenu()
            }
        },
        toggleTitleAndMenu() {
            if (this.menuVisible) {
                this.setSettingVisible(-1)
                this.setFontFamilyVisible(false)
            }
            this.setMenuVisible(!this.menuVisible)
        },
        initFontSize() {
            const fontSize = getFontSize(this.fileName)
            if (!fontSize) {
                saveFontSize(this.fileName, this.defaultFontSize)
            } else {
                this.setDefaultFontSize(fontSize)
                this.rendition.themes.fontSize(fontSize + 'px')
            }
        },
        initFontFamily() {
            const font = getFontFamily(this.fileName)
            if (!font) {
                saveFontFamily(this.fileName, this.defaultFontFamily)
            } else {
                this.setDefaultFontFamily(font)
                this.rendition.themes.font(font)
            }
        },
        initTheme() {
            let defaultTheme = getTheme(this.fileName)
            if (!defaultTheme) {
                defaultTheme = this.themeList[0].name
                saveTheme(this.fileName, defaultTheme)
            }
            this.setDefaultTheme(defaultTheme)
            // 注册样式
            this.themeList.forEach(theme => {
                this.rendition.themes.register(theme.name, theme.style)
            })
            // 初始化
            this.currentBook.rendition.themes.select(defaultTheme)
        },
        cbFunction() {
            // this.setBookAvailable(true)
            this.initFontSize()
            this.initFontFamily()
            this.initTheme()
            this.initGlobalStyle()
        },
        initRendition() {
            this.rendition = this.book.renderTo('read', {
                width: window.innerWidth,
                height: window.innerHeight,
                // 兼容微信
                method: 'default'
            })
            const location = getLocation(this.fileName)
            // 如果location是null，就是首次获取到本书，应该显示第一页
            if (location === null) {
                this.currentBook.rendition.display().then(() => {
                    this.setCurrentPageIndex(0)
                    this.setSection(0)
                    this.setProgress(0)
                    this.setPaginate(0)
                    this.cbFunction()
                })
            } else {
                this.display(location, null, this.cbFunction())
            }
            this.rendition.hooks.content.register(contents => {
                contents.addStylesheet(
                    `${process.env.VUE_APP_RES_URL}/fonts/daysOne.css`
                )
                contents.addStylesheet(
                    `${process.env.VUE_APP_RES_URL}/fonts/cabin.css`
                )
                contents.addStylesheet(
                    `${process.env.VUE_APP_RES_URL}/fonts/montserrat.css`
                )
                contents.addStylesheet(
                    `${process.env.VUE_APP_RES_URL}/fonts/tangerine.css`
                )
            })
        },
        initGesture() {
            this.rendition.on('touchstart', event => {
                this.touchStatX = event.changedTouches[0].clientX
                this.touchStartTime = event.timeStamp
            })
            this.rendition.on('touchend', event => {
                const offsetX =
                    event.changedTouches[0].clientX - this.touchStatX
                const time = event.timeStamp - this.touchStartTime
                if (time < 500 && offsetX > 40) {
                    this.prevPage()
                } else if (time < 500 && offsetX < -40) {
                    this.nextPage()
                } else {
                    this.toggleTitleAndMenu()
                }
                // event.preventDefault()
                event.stopPropagation()
            })
        },
        parseBook() {
            this.book.loaded.cover.then(cover => {
                this.book.archive.createUrl(cover).then(url => {
                    this.setCover(url)
                })
            })
            this.book.loaded.metadata.then(metadata => {
                this.setMetadata(metadata)
            })
            this.book.loaded.navigation.then(nav => {
                // 拉直导航，再分一级二级导航
                const navItem = flatten(nav.toc)
                function find(item, level = 0) {
                    if (!item.parent) {
                        return level
                    } else {
                        return find(
                            navItem.filter(
                                parentItem => parentItem.id === item.parent
                            )[0],
                            ++level
                        )
                    }
                }
                navItem.forEach(item => {
                    item.level = find(item)
                })
                this.setNavigation(navItem)
            })
        },
        initEpub(url) {
            this.book = new Epub(url)
            this.setCurrentBook(this.book)
            this.initRendition()
            this.initGesture()
            this.parseBook()
            // ready在book解析完成后调用
            this.book.ready
                .then(() => {
                    // 粗糙分页
                    return this.book.locations.generate(
                        750 *
                            (window.innerWidth / 375) *
                            (getFontSize(this.fileName) / 16)
                    )
                    // 因为这里是粗糙分页，导致下面的pagelist跟实际渲染的有些对不上
                })
                .then(locations => {
                    this.setPagelist(locations)
                    // locations相当于每页的一个标志，从哪一章哪几个字开始到什么时候结束是一页
                    // this.navigation.forEach(nav => {
                    //     nav.pagelist = []
                    // })
                    // // 遍历每页，看每页在哪个章节
                    // // pagelist存放本章的每页的locations
                    // locations.forEach(location => {
                    //     const loc = location.match(/\[(.*)\]!/)[1]
                    //     this.navigation.forEach(item => {
                    //         if (item.href) {
                    //             const href = item.href.match(/^(.*)\.html$/)
                    //             if (href && href[1] === loc) {
                    //                 item.pagelist.push(location)
                    //             }
                    //         }
                    //         // 用那个reg不全面，有些后缀可能是xhtml或别的，还是判断子字符串比较合理
                    //         // 还是不太对，可能跟电子书格式不完全统一有关
                    //         // 造成有的书正确，有的书有问题
                    //         // if (item.href && item.href.indexOf(loc) !== -1) {
                    //         //     item.pagelist.push(location)
                    //         // }
                    //     })
                    // })
                    // // 主要是为了数每章多少页，不用给每个nagation存储pagelist
                    // // 给每个章节增加page属性
                    // let currentPage = 1
                    // this.navigation.forEach((nav, index) => {
                    //     if (index === 0) {
                    //         nav.page = 1
                    //     } else {
                    //         nav.page = currentPage
                    //     }
                    //     currentPage += nav.pagelist.length + 1
                    // })
                    let currentPage = 1
                    for (let i = 0; i < this.navigation.length; i++) {
                        const nav = this.navigation[i]
                        nav.page = currentPage
                        if (currentPage < this.pagelist.length) {
                            // 遍历this.pageList，把本章节数完！！！

                            const cfi = this.pagelist[currentPage++].match(
                                /\[(.*)\]!/
                            )[1]
                            for (
                                ;
                                currentPage < this.pagelist.length;
                                currentPage++
                            ) {
                                const loc = this.pagelist[currentPage].match(
                                    /\[(.*)\]!/
                                )[1]
                                if (loc !== cfi) break
                            }
                        }
                    }
                    this.setBookAvailable(true)
                    // this.refreshLocation()
                    const location = getLocation(this.fileName)
                    if (location) {
                        this.myRefreshLocation(location)
                    } else {
                        this.display(this.pagelist[0])
                    }
                })
        }
    },
    mounted() {
        // 先找离线缓存的电子书中是否有
        const books = this.$route.params.fileName.split('|')
        const fileName = books[1]
        getLocalForage(fileName, (err, blob) => {
            if (!err && blob) {
                // 找到离线缓存电子书
                this.setFileName(books.join('/')).then(() => {
                    this.initEpub(blob)
                })
            } else {
                // 在线获取电子书
                this.setFileName(books.join('/')).then(() => {
                    const url = `${process.env.VUE_APP_EPUB_URL}/${this.fileName}.epub`
                    this.initEpub(url)
                })
            }
        })
    }
}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
// #id {
//     touch-action: none;
// }
@import '../../assets/styles/global';

.ebook-reader {
    width: 100%;
    height: 100%;
    overflow: hidden;
    .ebook-reader-mask {
        position: absolute;
        z-index: 150;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: transparent;
    }
}
</style>
