import { mapGetters, mapActions } from 'vuex'
import { themeList, addCss, removeAllCss, getReadTimeByMinute } from './book'
import {
    gotoBookDetail,
    appendAddToShelf,
    computeId,
    removeAddFromShelf
} from './store'
import {
    getBookmark,
    saveLocation,
    getBookshelf,
    saveBookShelf
} from './localStorage'
import { shelf } from '@/api/store'
import CreateToast from '@/components/common/Toast.js'

export const storeShelfMixin = {
    computed: {
        ...mapGetters([
            'isEditMode',
            'shelfList',
            'shelfSelected',
            'shelfTitleVisible',
            'offsetY',
            'shelfFooterShowVisible',
            'shelfFooterPopupProps',
            'shelfCategory',
            'currentType',
            'dialogProps'
        ])
    },
    methods: {
        ...mapActions([
            'setIsEditMode',
            'setShelfSelected',
            'setShelfTitleVisible',
            'setShelfList',
            'setOffsetY',
            'setShelfFooterShowVisible',
            'setShelfFooterPopupProps',
            'setShelfCategory',
            'setCurrentType',
            'setDialogProps'
        ]),
        showBookDetail(book) {
            gotoBookDetail(this, book)
        },
        getShelfList() {
            // 从缓存中获取
            let shelfList = getBookshelf()
            if (!shelfList) {
                // 获取书架数据
                shelf().then(response => {
                    if (
                        response.status === 200 &&
                        response.data &&
                        response.data.bookList
                    ) {
                        shelfList = appendAddToShelf(response.data.bookList)
                        saveBookShelf(shelfList)
                        return this.setShelfList(shelfList)
                    }
                })
            } else {
                return this.setShelfList(shelfList)
            }
        },
        getCategoryList(title) {
            // 先获取电子书列表
            this.getShelfList().then(() => {
                // 筛选出title对应的分组
                const categoryList = this.shelfList.filter(
                    book => book.type === 2 && book.title === title
                )[0]
                this.setShelfCategory(categoryList)
            })
        },
        moveOutOfGroup(f) {
            // 过滤书籍
            this.setShelfList(
                this.shelfList.map(book => {
                    if (book.type === 2 && book.itemList) {
                        book.itemList = book.itemList.filter(
                            // 没有选中的图书保留下来
                            subBook => !subBook.selected
                        )
                    }
                    return book
                })
            ).then(() => {
                // 把选中的电子书添加到书架最后
                const list = computeId(
                    appendAddToShelf(
                        [].concat(
                            removeAddFromShelf(this.shelfList),
                            ...this.shelfSelected
                        )
                    )
                )
                this.setShelfList(list).then(() => {
                    const toast = new CreateToast(
                        this.$t('shelf.moveBookOutSuccess')
                    )
                    toast.setTimeUnmount(1000)
                    if (f) f()
                })
            })
        }
    }
}

// 跟vue实例this有关的，混入实例中

export const storeHomeMixin = {
    computed: {
        ...mapGetters(['offsetY', 'hotSearchOffsetY', 'flapCardVisible'])
    },
    methods: {
        ...mapActions([
            'setOffsetY',
            'setHotSearchOffsetY',
            'setFlapCardVisible'
        ]),
        showBookDetail(book) {
            gotoBookDetail(this, book)
        }
    }
}

export const ebookMixin = {
    computed: {
        ...mapGetters([
            'fileName',
            'menuVisible',
            'settingVisible',
            'defaultFontSize',
            'defaultFontFamily',
            'fontFamilyVisible',
            'defaultTheme',
            'bookAvailable',
            'progress',
            'section',
            'isPaginating',
            'currentBook',
            'navigation',
            'cover',
            'metadata',
            'paginate',
            'pagelist',
            'offsetY',
            'isBookmark',
            'currentPageIndex'
        ]),
        themeList() {
            return themeList(this)
        },
        getSectionName() {
            if (this.navigation && this.navigation.length) {
                return this.section ? this.navigation[this.section].label : ''
            }
            return ''
        }
    },
    methods: {
        ...mapActions([
            'setFileName',
            'setMenuVisible',
            'setSettingVisible',
            'setDefaultFontSize',
            'setDefaultFontFamily',
            'setFontFamilyVisible',
            'setDefaultTheme',
            'setBookAvailable',
            'setProgress',
            'setSection',
            'setIsPaginating',
            'setCurrentBook',
            'setNavigation',
            'setCover',
            'setMetadata',
            'setPaginate',
            'setPagelist',
            'setOffsetY',
            'setIsBookmark',
            'setCurrentPageIndex'
        ]),
        initGlobalStyle() {
            removeAllCss()
            switch (this.defaultTheme) {
                case 'Default':
                    addCss(
                        `${process.env.VUE_APP_RES_URL}/theme/theme_default.css`
                    )
                    break
                case 'Gold':
                    addCss(
                        `${process.env.VUE_APP_RES_URL}/theme/theme_gold.css`
                    )
                    break
                case 'Eye':
                    addCss(`${process.env.VUE_APP_RES_URL}/theme/theme_eye.css`)
                    break
                case 'Night':
                    addCss(
                        `${process.env.VUE_APP_RES_URL}/theme/theme_night.css`
                    )
                    break
                default:
                    addCss(
                        `${process.env.VUE_APP_RES_URL}/theme/theme_default.css`
                    )
                    break
            }
        },
        refreshLocation() {
            const currentLocation = this.currentBook.rendition.currentLocation()
            if (currentLocation && currentLocation.start) {
                const startCfi = currentLocation.start.cfi
                const progress = this.currentBook.locations.percentageFromCfi(
                    currentLocation.start.cfi
                )
                // currentLocation.start是只能找到一级目录的index吗
                this.setSection(currentLocation.start.index) // 是不是书籍不够完整
                this.setProgress(Math.floor(progress * 100))
                saveLocation(this.fileName, startCfi)
                const bookmark = getBookmark(this.fileName)
                if (bookmark) {
                    if (bookmark.some(item => item.cfi === startCfi)) {
                        this.setIsBookmark(true)
                    } else {
                        this.setIsBookmark(false)
                    }
                } else {
                    this.setIsBookmark(false)
                }
                if (this.pagelist) {
                    const totalPage = this.pagelist.length
                    const currentPage = currentLocation.start.location
                    if (currentPage && currentPage > 0) {
                        this.setPaginate(currentPage + '/' + totalPage)
                    } else {
                        this.setPaginate('')
                    }
                } else {
                    this.setPaginate('')
                }
            }
        },
        // 可以重写refreshLocation，看那些量是否可以不用通过currentLocation取得
        // 看那些地方有调用display，是否可以传入section、paginate等，
        // paginate可以确定progress，主要是改变哪些变量，是否可以从其余途径得到
        display(target, page, cb) {
            if (target) {
                return this.currentBook.rendition.display(target).then(() => {
                    // this.refreshLocation()
                    this.myRefreshLocation(target, page)
                    if (cb) cb()
                })
            }
            //  else {
            //     // 如果是空，应该是第一次进入
            //     return this.currentBook.rendition.display().then(() => {
            //         // this.refreshLocation()
            //         this.myRefreshLocation()
            //         if (cb) cb()
            //     })
            // }
        },
        // 如果给定的是epubcfi，可以直接根据epubcfi确定progress、章节等
        myRefreshLocation(location, page) {
            if (location && this.pagelist) {
                let index = 0
                if (page) {
                    index = page
                    location = this.pagelist[page]
                } else {
                    for (; index < this.pagelist.length; index++) {
                        if (this.pagelist[index] === location) {
                            break
                        }
                    }
                }
                this.setCurrentPageIndex(index)
                index++
                let section = 0
                for (; section < this.navigation.length - 1; section++) {
                    if (
                        index >= this.navigation[section].page &&
                        index < this.navigation[section + 1].page
                    ) {
                        break
                    }
                }
                this.setSection(section)
                this.setProgress(
                    Math.floor((index / this.pagelist.length) * 100)
                )
                saveLocation(this.fileName, location)
                const bookmark = getBookmark(this.fileName)
                if (bookmark) {
                    if (
                        bookmark.some(
                            item => item.currentPageIndex === index - 1
                        )
                    ) {
                        this.setIsBookmark(true)
                    } else {
                        this.setIsBookmark(false)
                    }
                } else {
                    this.setIsBookmark(false)
                }
                this.setPaginate(index + '/' + this.pagelist.length)
            }
        },
        hideTitleAndMenu() {
            this.setMenuVisible(false)
            this.setSettingVisible(-1)
            this.setFontFamilyVisible(false)
        },
        getReadTimeText() {
            return this.$t('book.haveRead').replace(
                '$1',
                getReadTimeByMinute(this.fileName)
            )
        }
    }
}
