class CreateToast {
    constructor(text) {
        this.messageInstance = createApp(toast, {
            text
        })
        // 挂载节点
        this.mountNode = document.createElement('div')
        document.body.appendChild(this.mountNode)
        this.messageInstance.mount(this.mountNode)
    }

    myUnmount() {
        // 卸载节点
        this.messageInstance.unmount()
        document.body.removeChild(this.mountNode)
    }

    setTimeUnmount(timeout) {
        setTimeout(() => {
            this.myUnmount()
        }, timeout)
    }

    // 如何更新toat，不停地卸载安装，效率很低
}

export default CreateToast
