<template>
    <div class="ebook-loading">
        <div class="ebook-loading-wrapper">
            <div
                class="ebook-loading-item"
                v-for="(item, index) in data"
                :key="index"
            >
                <div
                    class="ebook-loading-line-wrapper"
                    v-for="(subItem, subIndex) in item"
                    :key="subIndex"
                >
                    <div class="ebook-loading-line" :ref="setLineRef"></div>
                    <div class="ebook-loading-mask" :ref="setMaskRef"></div>
                </div>
            </div>
            <div class="ebook-loading-center"></div>
        </div>
    </div>
</template>
<script>
import { px2rem } from '@/utils/utils'
export default {
    data() {
        return {
            data: [
                [{}, {}, {}],
                [{}, {}, {}]
            ],
            lines: [],
            masks: [],
            maskWidth: [
                { value: 0 },
                { value: 0 },
                { value: 0 },
                { value: 0 },
                { value: 0 },
                { value: 0 }
            ],
            lineWidth: [
                { value: 16 },
                { value: 16 },
                { value: 16 },
                { value: 16 },
                { value: 16 },
                { value: 16 }
            ],
            add: true,
            end: false
        }
    },
    mounted() {
        this.task = setInterval(() => {
            for (const index in this.masks) {
                const mask = this.masks[index]
                const line = this.lines[index]
                const maskWidthItem = this.maskWidth[index]
                const lineWidthItem = this.lineWidth[index]
                if (index === '0') {
                    if (this.add && maskWidthItem.value < 16) {
                        maskWidthItem.value++
                        lineWidthItem.value--
                    } else if (!this.add && lineWidthItem.value < 16) {
                        maskWidthItem.value--
                        lineWidthItem.value++
                    }
                } else {
                    if (this.add && maskWidthItem.value < 16) {
                        const preMaskWidth = this.maskWidth[index - 1]
                        if (preMaskWidth.value >= 8) {
                            maskWidthItem.value++
                            lineWidthItem.value--
                        }
                    } else if (!this.add && lineWidthItem.value < 16) {
                        const preLineWidth = this.lineWidth[index - 1]
                        if (preLineWidth.value >= 8) {
                            maskWidthItem.value--
                            lineWidthItem.value++
                        }
                    }
                }

                mask.style.width = `${px2rem(maskWidthItem.value)}rem`
                mask.style.flex = `0 0 ${px2rem(maskWidthItem.value)}rem`
                line.style.width = `${px2rem(lineWidthItem.value)}rem`
                line.style.flex = `0 0 ${px2rem(lineWidthItem.value)}rem`
                if (index === '5') {
                    if (this.add) {
                        if (maskWidthItem.value === 16) {
                            this.end = true
                        }
                    } else {
                        if (maskWidthItem.value === 0) {
                            this.end = true
                        }
                    }
                    if (this.end) {
                        this.add = !this.add
                        this.end = false
                    }
                }
            }
        }, 20)
    },
    beforeUnmount() {
        if (this.task) {
            clearInterval(this.task)
        }
        this.lines = []
        this.masks = []
    },
    methods: {
        setLineRef(el) {
            this.lines.push(el)
        },
        setMaskRef(el) {
            this.masks.push(el)
        }
    }
}
</script>

<style lang="scss" scoped>
@import '@/assets/styles/global';
.ebook-loading {
    position: relative;
    z-index: 400;
    width: px2rem(63);
    height: px2rem(40);
    background: transparent;
    border: px2rem(1.5) solid #d7d7d7;
    border-radius: px2rem(3);
    .ebook-loading-wrapper {
        display: flex;
        width: 100%;
        height: 100%;
        .ebook-loading-item {
            display: flex;
            flex-direction: column;
            flex: 1;
            padding: px2rem(7) 0;
            .ebook-loading-line-wrapper {
                flex: 1;
                padding: 0 px2rem(7);
                box-sizing: border-box;
                @include left;
                .ebook-loading-line {
                    flex: 0 0 px2rem(16);
                    width: px2rem(16);
                    height: px2rem(2);
                    background: #d7d7d7;
                }
                .ebook-loading-mask {
                    flex: 0 0 0;
                    width: 0;
                    height: px2rem(2);
                }
            }
        }
        .ebook-loading-center {
            position: absolute;
            left: 50%;
            top: 0;
            width: px2rem(1.5);
            height: 100%;
            background: #d7d7d7;
        }
    }
}
</style>
